import V from '../../utils/validate'
export default{
	data(){
		return {
			mobile: '',
			password: '',
			repeatPassword: '',
			logining: false
		}
	},
	onLoad(){
		
	},
	methods: {
		inputChange(e){
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		navBack(){
			uni.navigateBack();
		},
		async toRegister(){
			// this.logining = true;
			const {mobile, password, repeatPassword} = this;
			// 数据验证模块
			if(!V.tel(mobile)) {
				this.$api.msg('请输入正确手机号！');
				this.logining = false;
				return;
			} else if (!V.password(password)) {
				this.$api.msg('密码至少包含数字跟字母，可以有字符,长度6-20!');
				this.logining = false;
				return;
			} else if (!V.isSame(password,repeatPassword)) {
				this.$api.msg('两次密码不一致!');
				this.logining = false;
				return;
			}
			const data = await this.Api.register(mobile,password,repeatPassword)
			if(data.code == 1) {
				this.$api.msg('注册成功!');
				setTimeout(()=>{
					uni.redirectTo({
						url: '/pages/public/login'
					});
				},1500)
			}
		}
	},

}
